"use client";

import { useState, useEffect } from "react";
import { PlusIcon } from '@heroicons/react/24/outline';
import { addItem } from 'components/cart/actions';
import { useCart } from 'components/cart/cart-context';
import { useProduct } from 'components/product/product-context';
import { Product } from 'lib/shopify/types';
import { useActionState } from 'react';
import { getSellerById } from 'lib/demo/sellers';

type Props = {
  listingId: string;
  product: Product;
  sellerId: string;
  declaredValueAed?: number;
  weightKg?: number;
  fragile?: boolean;
};

export default function DeliveryWidget(props: Props) {
  const seller = getSellerById(props.sellerId);
  const { addCartItem } = useCart();
  const { state } = useProduct();
  const [message, formAction] = useActionState(addItem, null);
  
  const [pickupArea, setPickupArea] = useState(seller?.location || "Dubai");
  const [dropoffArea, setDropoffArea] = useState("Dubai");
  const [quote, setQuote] = useState<any>(null);
  const [serviceLevel, setServiceLevel] = useState<"economy" | "standard" | "express">("standard");
  const [tracking, setTracking] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  
  // Get selected variant
  const variant = props.product.variants.find((v) =>
    v.selectedOptions.every(
      (option) => option.value === state[option.name.toLowerCase()]
    )
  );
  const defaultVariantId = props.product.variants.length === 1 ? props.product.variants[0]?.id : undefined;
  const selectedVariantId = variant?.id || defaultVariantId;
  const finalVariant = props.product.variants.find(
    (v) => v.id === selectedVariantId
  );
  
  const addItemAction = selectedVariantId ? formAction.bind(null, selectedVariantId) : null;

  async function getQuote() {
    setLoading(true);
    setTracking(null);
    try {
      const res = await fetch("/api/rockets/quote", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          listingId: props.listingId,
          pickupArea,
          dropoffArea,
          declaredValueAed: props.declaredValueAed ?? 300,
          weightKg: props.weightKg ?? 3,
          fragile: props.fragile ?? false
        })
      });
      const quoteData = await res.json();
      setQuote(quoteData);
      // Auto-select first service level if available
      if (quoteData?.options?.length > 0) {
        setServiceLevel(quoteData.options[0].service_level);
      }
    } catch (error) {
      console.error("Error fetching quote:", error);
    } finally {
      setLoading(false);
    }
  }

  // Auto-load quote on mount and when dropoffArea changes
  useEffect(() => {
    if (pickupArea && dropoffArea) {
      const fetchQuote = async () => {
        setLoading(true);
        setTracking(null);
        try {
          const res = await fetch("/api/rockets/quote", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
              listingId: props.listingId,
              pickupArea,
              dropoffArea,
              declaredValueAed: props.declaredValueAed ?? 300,
              weightKg: props.weightKg ?? 3,
              fragile: props.fragile ?? false
            })
          });
          const quoteData = await res.json();
          setQuote(quoteData);
          // Auto-select first service level if available
          if (quoteData?.options?.length > 0) {
            setServiceLevel(quoteData.options[0].service_level);
          }
        } catch (error) {
          console.error("Error fetching quote:", error);
        } finally {
          setLoading(false);
        }
      };
      fetchQuote();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dropoffArea, pickupArea, props.listingId, props.declaredValueAed, props.weightKg, props.fragile]); // Re-fetch when relevant props change

  async function book() {
    setLoading(true);
    const res = await fetch("/api/rockets/book", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ listingId: props.listingId, serviceLevel, pickupArea, dropoffArea })
    });
    const booked = await res.json();

    const tr = await fetch(`/api/rockets/track?tracking_id=${encodeURIComponent(booked.tracking_id)}`);
    setTracking(await tr.json());
    setLoading(false);
  }

  return (
    <div className="mt-6 rounded-2xl border border-neutral-200 p-4">
      <div className="flex items-start justify-between gap-4">
        <div>
          <div className="text-sm font-semibold">ðŸšš Delivery by Rockets</div>
          <div className="text-xs text-neutral-600">Peer-to-peer item pickup â†’ delivery to buyer</div>
        </div>
      </div>

      <div className="mt-3 flex flex-wrap gap-3">
        <label className="grid gap-1 flex-1 min-w-[150px]">
          <span className="text-xs text-neutral-600">Pickup Area</span>
          <div className="rounded-lg border px-2 py-1 text-sm bg-neutral-50 dark:bg-neutral-800 text-neutral-700 dark:text-neutral-300">
            {seller?.address || pickupArea}
          </div>
        </label>

        <label className="grid gap-1 flex-1 min-w-[150px]">
          <span className="text-xs text-neutral-600">Delivery Address</span>
          <select 
            className="rounded-lg border px-2 py-1 text-sm" 
            value={dropoffArea} 
            onChange={(e) => setDropoffArea(e.target.value)}
            disabled={loading}
          >
            {["Dubai", "Sharjah", "Abu Dhabi", "Ajman", "RAK"].map((x) => <option key={x}>{x}</option>)}
          </select>
        </label>
      </div>

      {loading && !quote ? (
        <div className="mt-4 text-center text-sm text-neutral-600">Loading delivery options...</div>
      ) : quote?.options?.length ? (
        <div className="mt-4 grid gap-2">
          {quote.options.map((o: any) => (
            <label key={o.service_level} className="flex items-center gap-3 rounded-xl border p-3 cursor-pointer hover:bg-neutral-50 dark:hover:bg-neutral-800">
              <input
                type="radio"
                name="service"
                checked={serviceLevel === o.service_level}
                onChange={() => setServiceLevel(o.service_level)}
                className="cursor-pointer"
              />
              <div className="flex-1">
                <div className="text-sm font-semibold">{String(o.service_level).toUpperCase()}</div>
                <div className="text-xs text-neutral-600">ETA: {o.eta_hours}h Â· Insurance: {o.insurance_included ? "Yes" : "No"}</div>
              </div>
              <div className="text-sm font-bold">{quote.currency} {o.price}</div>
            </label>
          ))}

          {finalVariant && addItemAction ? (
            <form
              action={async () => {
                // Get selected delivery option price
                const selectedDeliveryOption = quote.options.find((o: any) => o.service_level === serviceLevel);
                const deliveryPrice = selectedDeliveryOption ? Number(selectedDeliveryOption.price) : 0;
                
                // Optimistic update with delivery price
                addCartItem(finalVariant, props.product, deliveryPrice);
                // Server action
                addItemAction();
              }}
              className="mt-2"
            >
              <button
                type="submit"
                className="relative flex w-full items-center justify-center rounded-xl bg-blue-600 px-4 py-3 text-sm font-bold text-white transition-colors hover:bg-blue-700 disabled:opacity-60 dark:bg-blue-500 dark:hover:bg-blue-600"
                disabled={loading || !selectedVariantId || !props.product.availableForSale}
              >
                <div className="absolute left-0 ml-4">
                  <PlusIcon className="h-5" />
                </div>
                Add to Cart with Delivery
              </button>
              <p aria-live="polite" className="sr-only" role="status">
                {message}
              </p>
              {message && (
                <p className="mt-2 text-sm text-green-600 dark:text-green-400" role="status">
                  {message}
                </p>
              )}
            </form>
          ) : (
            <button
              disabled
              className="relative flex w-full items-center justify-center rounded-xl bg-neutral-400 px-4 py-3 text-sm font-bold text-white cursor-not-allowed opacity-60"
            >
              <div className="absolute left-0 ml-4">
                <PlusIcon className="h-5" />
              </div>
              Please select variant
            </button>
          )}
          
          <div className="mt-2 text-xs text-neutral-500">
            Total: {quote.currency} {quote.options.find((o: any) => o.service_level === serviceLevel)?.price || quote.options[0]?.price} (delivery included)
          </div>

          {quote.disclaimer && (
            <div className="text-xs text-neutral-500">{quote.disclaimer}</div>
          )}
        </div>
      ) : quote && !quote.options?.length ? (
        <div className="mt-4 text-center text-sm text-neutral-600">No delivery options available</div>
      ) : null}

      {tracking?.timeline?.length ? (
        <div className="mt-4">
          <div className="text-sm font-semibold">Tracking: {tracking.tracking_id}</div>
          <div className="text-xs text-neutral-600">Current: {tracking.current_status}</div>

          <div className="mt-2 grid gap-1">
            {tracking.timeline.map((t: any, i: number) => (
              <div key={i} className="text-xs">
                <span className="text-neutral-500">{new Date(t.ts).toLocaleString()}</span>{" "}
                â€” <span className="font-semibold">{t.status}</span> â€” {t.note}
              </div>
            ))}
          </div>
        </div>
      ) : null}
    </div>
  );
}
